import React from 'react';
import { FaSlidersH } from "react-icons/fa";
import MobileFilters from './mobile-filters';
import Categories from './categories';

const Filters = ({ books, setBooks, showFilters, mobile, setShowFilters, bookRec, fetchBooks, setBookRec }) => {

const handleSelection = (category) => {
	fetchBooks();
	console.log(category);
	let filtered = books.filter((book) => book.category === category);
	console.log('filtered: ' + filtered);
	let random = Math.floor(Math.random() * filtered.length);
	console.log('random: ' + random)
	setBookRec(random);
	setBooks(filtered);
}
	if (showFilters && mobile) {
		return (
			<MobileFilters showFilters={showFilters} setShowFilters={setShowFilters}></MobileFilters>
			)
	} else if (mobile) {
		return null;
	} else if (bookRec === -1){
		return null;
	} else {
		return (
			<div className='categories'>
				<h4>Refine results  <FaSlidersH style={{verticalAlign: 'middle'}} /></h4>
				<form>
				{Categories.map((category, index) => {
					return (
						<>
						<input type="radio" id={category} name='categories' value={category}  key={index} onClick={() => handleSelection(category)} />
						<label htmlFor={category}>{category}</label><br/>
						</>
						)
				})}
				</form>
			</div>
		)
	}
};

export default Filters;