import React from 'react';

const Eros = ({bookRec}) => {
	if (bookRec === -1) {
		return (
			<>
			<div>
				<img className='eros' src={process.env.PUBLIC_URL + `/images/eros.svg`} alt="Eros"/>
				<img className='arrow' src={process.env.PUBLIC_URL + `/images/arrow.svg`} alt="Arrow"/>
				<div className='heart'></div>
			</div>
			</>
		)
	} else {
		return null;
	}
}
export default Eros;