import React, { useState, useEffect } from 'react';
import Rec from './rec';
import Books from './books';
import Filters from './filters';
import Eros from './eros';
import { FaSlidersH } from "react-icons/fa";

const Home = () => {
	const [books, setBooks] = useState([]);
	const [bookRec, setBookRec] = useState(-1);
	const [showFilters, setShowFilters] = useState(false);
	const [mobile, setMobile] = useState(false);

	const fetchBooks = () => {
			fetch('http://localhost:8000/users')
			.then(res => res.json())
			.then(bookList => setBooks(bookList))
			.catch(err => console.log(err))

			console.log('fetched');
	}	

	useEffect(() => {
		fetchBooks();
	},[])

	useEffect(() => {
		mobileFilters();
	})

	const mobileFilters = () => {window.innerWidth < 800 ? setMobile(true) : setMobile(false)};
	window.addEventListener('resize', mobileFilters);

	const handleSubmit = (e) => {
		e.preventDefault();
		let random = Math.floor(Math.random() * books.length);
		setBookRec(random);
	};

	const handleFilters = (e) => {
		e.preventDefault();
		setShowFilters(true);
	}

	return (
		<>
		<section className="hero">
			<h1>Eros Recommends</h1>
			<h4>Romance novels you'll love at first click.</h4>
		</section>
		<Eros bookRec={bookRec}></Eros>
		<section className="rec-interaction">
			<div className='hero-btns'>
				<button className='btn filter-btn' onClick={handleFilters}><FaSlidersH style={{verticalAlign: 'middle'}}/></button>
				<button type='submit' className='btn random-btn' onClick={handleSubmit}>Tell me what to read</button>
			</div>
			<article className='book-results'>
				<Filters setBookRec={setBookRec} fetchBooks={fetchBooks} books={books} setBooks={setBooks} setShowFilters={setShowFilters} mobile={mobile} showFilters={showFilters} bookRec={bookRec}></Filters>
				<Rec bookRec={bookRec} books={books}></Rec>
			</article>
		</section>
		</>
	)
};

export default Home;